import { Component, Input, OnInit } from '@angular/core';
import { RBACINFO } from './pics-chat-redbird-ai/@core/urls/chat-redbird-ai-url.config';
import { Observable } from 'rxjs';
import { PermissionStore } from './pics-chat-redbird-ai/@core/permissions/permission.store';
import { DataStoreService } from './pics-chat-redbird-ai/@core/service/data-store.service';

@Component({
  selector: 'chat-redbird-ai',
  template: `
      <app-chat-widget botName="Cardinality" theme="blue"></app-chat-widget>
  `,
  styles: [
  ]
})
export class ChatRedbirdAiComponent implements OnInit {

  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public chatEvent!: Observable<any>;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService
  ) {
  }
  ngOnInit() {
     this.chatEvent.subscribe((val: any) => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
    })
  }

}
