import { Component, ElementRef, HostListener, Input, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Subject, Subscription } from 'rxjs';
import { fadeIn, fadeInOut } from '../animations';
import { ChatBotRasaService } from '../../@core/service/chatbot-rasa.service';
import { debounceTime } from 'rxjs/operators';
import { RBACINFO } from '../../@core/urls/chat-redbird-ai-url.config';
import { DataStoreService } from '../../@core/service/data-store.service';

declare let $: any;
const rand = (max: any) => Math.floor(Math.random() * max);

@Component({
  selector: 'app-chat-widget',
  templateUrl: './chat-widget.component.html',
  styleUrls: ['./chat-widget.component.scss'],
  animations: [fadeInOut, fadeIn]
})
export class ChatWidgetComponent implements OnInit {
  @ViewChild('bottom') bottom!: ElementRef;
  @Input() public theme: 'blue' | 'grey' | 'red' = 'blue';
  @Input() public botName = 'Bot';
  @Input() public botAvatar = '../../../../assets/images/redbird_ai.png';
  @Input() public userAvatar = '../../../../assets/images/chat_avator.png';
  @Input() url: any;
  @Input() public startingMessage: any;
  @Input() public opened = false;
  spin = false;
  menuList = [];
  openFeedback = false;
  selectedmessage = '';
  selectQuestion = '';
  public _visible = false;
  private chatService: ChatBotRasaService;
  environment: any;
  RBACORG: RBACINFO = new RBACINFO();
  PERMISSION: any;
  orgSubs!: Subscription;
  orgId: any;

  constructor(chatService: ChatBotRasaService, private _storeservice: DataStoreService) {
    this.chatService = chatService;
    this.orgSubs =  this._storeservice.currentStore.subscribe((res: any) => {
      if (res['RBACORG'] && res['RBACORG'] !== '') {
        this.RBACORG = res['RBACORG'];
        this.environment = this.RBACORG['environment'];
        this.orgId = parseInt(this.RBACORG['orgID']);
        if(this.environment){
          this.chatService.connect(this.environment['ChatBot']);
        }
      }
    });
  }

  public get visible() {
    return this._visible;
  }

  @Input() public set visible(visible) {
    this._visible = visible;
    if (this._visible) {
      setTimeout(() => {
        this.scrollToBottom();
        this.focusMessage();
      }, 0);
    }
  }

  public focus = new Subject();
  public clearSearch = new Subject();

  public operator: any;

  public client: any;

  public messages: any[] = [];
  currentMessages: any[] = [];
  sele: any;
  chart: any;
  public addMessage(from: any, text: any, type: 'received' | 'sent', islike: boolean, isdislike: boolean, question: any) {
    this.spin = false;
    this.messages.unshift({
      from,
      text,
      type,
      date: new Date().getTime(),
      islike,
      isdislike,
      question
    });
    this.currentMessages.unshift({
      from,
      text,
      type,
      date: new Date().getTime(),
      islike,
      isdislike,
      question
    });
    this.scrollToBottom();
  }

  public scrollToBottom() {
    if (this.bottom !== undefined) {
      this.bottom.nativeElement.scrollIntoView();
    }
  }

  public focusMessage() {
    this.focus.next(true);
  }

  ngOnInit() {
    this.initialize();
  }
  initialize() {
    this.startingMessage = 'Hi, how can we help you?';
    this.client = {
      name: 'Guest User',
      status: 'online',
      avatar: this.userAvatar
    };

    this.operator = {
      name: this.botName,
      status: 'online',
      avatar: this.botAvatar
    };
    if (this.opened) {
      setTimeout(() => (this.visible = true), 1000);
    }
    setTimeout(() => {
      this.addMessage(this.operator, this.startingMessage, 'received', false, false, '');
    }, 1500);
    // setTimeout(() => {
    //   const startingMessage = {
    //     data: ['Referrals', 'Expenses'],
    //     filter: '',
    //     msg: 'Would you like to check the below items ?',
    //     response_type: 'questions',
    //     url: ''
    //   };
    //   this.addMessage(this.operator, startingMessage, 'received', false, false, '');
    // }, 1500);
    this.chatService.getMessages().subscribe((message: any) => {
      setTimeout(() => {
        this.spin = false;
        const msg = isValidJSONString(message.text) ? JSON.parse(message.text) : message.text;
        this.addMessage(this.operator, msg, 'received', false, false, this.selectQuestion);
        setTimeout(() => {
          this.scrollToBottom();
          this.focusMessage();
        }, 0);
      }, 500);
    });
  }

  public toggleChat() {
    this.visible = !this.visible;
  }

  public sendMessage({ message }: any) {
    this.selectQuestion = message;
    if (message.trim() === '') {
      return;
    }
    const currentMessage = this.sendMenuKey(message);
    this.addMessage(this.client, message, 'sent', false, false, this.selectQuestion);
    this.spin = true;
    this.chatService.sendMessage(currentMessage);
  }
  public searchMessage({ message }: any) {
    if (message.trim() === '') {
      return;
    }
    this.chatService
      .searchMenus(message)
      .pipe(debounceTime(2000))
      .subscribe((x: any) => {
        if (x) {
          this.menuList = x;
        }
      });
  }
  @HostListener('document:keypress', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    if (event.key === '/') {
      this.focusMessage();
    }
    if (event.key === '?' && !this._visible) {
      this.toggleChat();
    }
  }
  closeChatBot() {
    this.messages = [];
    this.chatService.disconnect();
    this.startingMessage = 'Hi, how can we help you?';
  }
  showMessage(message: any): any {
    if (message.text && message.text.response_type) {
      return message.text.response_type === 'table' ||
        message.text.response_type === 'text' ||
        message.text.response_type === 'questions' ||
        message.text.response_type === 'link'
        ? true
        : false;
    }
    return false;
  }
  showTable(message: any, type: any): any {
    if (message.text && message.text.response_type) {
      return message.text.response_type === type ? true : false;
    }
    return false;
  }
  getHeaderName(message: any): any {
    if (message.text && message.text.data) {
      return message.text.data[0];
    }
    return {};
  }
  getColumnData(message: any): any {
    if (message.text && message.text.data) {
      return message.text.data;
    }
    return [];
  }
  raiseQuestions(message: any) {
    this.sendMessage({ message });
    setTimeout(() => {
      this.scrollToBottom();
      this.focusMessage();
    }, 0);
  }
  selectMessage(message: any) {
    this.toggleShow();
    this.toClearSearch();
    this.sendMessage({ message });
    setTimeout(() => {
      this.scrollToBottom();
      this.focusMessage();
    }, 0);
  }
  toClearSearch() {
    this.clearSearch.next(true);
  }
  downloadFile(message: any) {
    if (message.text.url) {
      window.open(message.text.url, '_blank');
    }
  }

  toggleShow() {
    this.menuList = [];
  }
  activeLike(message: any) {
    this.selectedmessage = message;
    this.currentMessages = this.currentMessages.map(x => {
      return {
        type: x.type,
        text: x.text,
        islike: x.text === message.text ? true : x.islike,
        isdislike: x.text === message.text ? false : x.isdislike,
        from: x.from,
        date: x.date,
        question: x.question
      };
    });
    this.openFeedback = true;
  }
  activeDisLike(message: any) {
    this.selectedmessage = message;
    this.currentMessages = this.currentMessages.map(x => {
      return {
        type: x.type,
        text: x.text,
        islike: x.text === message.text ? false : x.islike,
        isdislike: x.text === message.text ? true : x.isdislike,
        from: x.from,
        date: x.date,
        question: x.question
      };
    });
    this.openFeedback = true;
  }
  getLikeStatus(message: any) {
    const isLike = this.currentMessages.filter(x => x.text === message.text && x.islike);
    return isLike.length > 0 ? true : false;
  }
  getDisLikeStatus(message: any) {
    const isDisLike = this.currentMessages.filter(x => x.text === message.text && x.isdislike);
    return isDisLike.length > 0 ? true : false;
  }
  sendFeedback(message: any) {
    const feedbackQue = this.currentMessages.filter(x => x.text === message.text);
    if (feedbackQue.length > 0) {
      this.chatService.sendFeedback(feedbackQue[0]).subscribe(_x => {
        //not to be empty
      });
    }
    this.closeFeedback();
  }
  closeFeedback() {
    this.openFeedback = false;
  }
  sendMenuKey(message: any) {
    if (message === 'Referrals') {
      return 'orref menu';
    } else if (message === 'Expenses') {
      return 'orexp menu';
    } else {
      return message;
    }
  }
  getChatBotInfo(_modal: any){

  }
}

function isValidJSONString(str: any) {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
}
